
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Card from 'vux/src/components/card/index.vue'

export default {
name: "myAddress",
components: {
ViewBox,
XHeader,
Card
},
data() {
return {
addressList: []
};
},
created () {
this.queryUserAddress();
},
methods: {
queryUserAddress() {
this.$api
.queryUserAddress({})
.then(data => {
if (data.code === 200) {
this.addressList = data.data.address;
}
});
}
}
};



